<template>
  <!-- <div>
    <img class="devices" 
    v-show="isLodeDevices" 
    v-for="(image,index) in getShowDevices" 
    :key="index" 
    :src="image" 
    alt="Image"
    @click="connectDevice(index)">
  </div> -->

  <el-carousel :interval="4000" type="card" height="380px">
    <el-carousel-item v-show="isLodeDevices"  v-for="(image,index) in getShowDevices" :key="index">
      <img :src="image" @click="connectDevice(index)">
    </el-carousel-item>
  </el-carousel>
</template>

<script>
  import HIDHandle from './HIDHandle'
  export default {
    data() {
      return {
        devicesPath:[
          './img/1Home/dev1online.png'
        ],
        isLodeDevices:false,
        devicesInfo:[]
      }
    },
    methods: {
      async connectDevice(index) {
        var connect = false;
        connect = await HIDHandle.requestDevice(index,this.devicesInfo);
        if(connect)
          this.$bus.$emit('enterMain',true);
      }
    },
    computed: {
      getShowDevices() {
        if(this.devicesPath.length > 0)
        {
          return this.devicesPath;
        }
        else
        {
          return null;
        }
      }
    },
    mounted() {
      this.$bus.$on('getShowDevices',value => {
        for(var i = 0;i < value.length;i++)
        {
          this.devicesPath[i] = './img/1Home/dev' + (i+1) + 'online.png';
        }
        this.devicesInfo = value,
        this.isLodeDevices = true;
        console.log('showDevices:',this.devicesPath,this.devicesInfo);
      })
    },
    beforeDestroy() {
      this.$bus.$off('getShowDevices');
    }
  }
</script>

<style lang="less" scoped>
  .devices {
    margin-left: 20px;
    margin-top: 20px;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>