<template>
  <div>
    <el-row>
      <span>DPI级数</span>
      <select name="dpi" @change="changeMaxPDI">
        <option 
        v-for="item in dpis" 
        :key="item.index"
        value="item.index">
        {{ item.index+1 }}
      </option>
      </select>
    </el-row>
    <el-row>
      <el-slider 
        style="left:8%;position: absolute; width: 80%; height: 1px;"
        v-model="dpiValue" 
        show-input
        :min="50"
        :max="26000"
        :step="50"
        @change="ChangeDPIValue">
      </el-slider>
    </el-row>
    <el-row style="margin-top: 5%;">
      <el-radio 
        v-for="item in dpis"
        :key="item.index"
        v-model="dpiIndex" 
        :label="item.index" 
        style="color:white"
        @input="ChangeDPIIndex"
        border>
        {{ item.value }}
        </el-radio>
    </el-row>

    <el-row >

          <el-color-picker 
          v-for="item in dpis" 
          :key="item.index"
          v-model="color1" 
          size="mini" 
          style="margin-right: 150px;"></el-color-picker>


    </el-row>


  </div>
</template>

<script>
  import HIDHandle from './HIDHandle';

  export default {
    data() {
      return {
        color1:'#fff',
        colors:[
          '#FFF',
          '#FFF',
          '#FFF',
          '#FFF',
          '#FFF',
          '#FFF',
          '#FFF',
          '#FFF',
        ],
        dpiIndex: 0,
        dpiValue:400,
        dpis: [
          {
            index:0,
            value:400,
            color:"#FFF"
          },
          {
            index:1,
            value:800,
            color:"#FFF"
          },   
          {
            index:2,
            value:1600,
            color:"#FFF"
          },
          {
            index:3,
            value:3200,
            color:"#FFF"
          },  
          {
            index:4,
            value:6400,
            color:"#FFF"
          },
          {
            index:5,
            value:8000,
            color:"#FFF"
          },   
          {
            index:6,
            value:10000,
            color:"#FFF"
          },
          {
            index:7,
            value:12000,
            color:"#FFF"
          },        
        ]
      }
    },
    methods: {
      ChangeDPIValue() {
        this.dpis[this.dpiIndex].value = this.dpiValue;
      },
      ChangeDPIIndex() {
        this.dpiValue = this.dpis[this.dpiIndex].value;
      },
      changeMaxPDI(e) {
        console.log(e);
        //HIDHandle.setMaxDPI()
      }
    }
  }
</script>

<style lang="less" scoped>
.el-radio.is-bordered {
  margin-right: 60px;
  width: 100px;
  height: 100px;
}
/deep/.el-radio .el-radio__input {
    display: none;
    color:white;
}

.el-radio.is-bordered {
  border-radius: 10px;
  margin-left: 3%;
}

.el-radio.is-bordered.is-checked {
  border-color: white;
  width: 120px;
  height: 120px;
}

/deep/.el-radio .el-radio__label {
  color:white;
  padding-top: 50px;
}
</style>