<template>
  <div id="app">
    <welcome v-show="!isHome && isSupportHID"></welcome>
    <home v-show="isHome && isSupportHID"></home> 
    <browser-page v-show="!isSupportHID"></browser-page>
  </div>
</template>

<script>
  import Welcome from './views/Welcome'
  import Home from './views/Home'
  import BrowserPage from './components/BrowserPage'
  
  export default {
    name: 'App',
    data() {
      return {
        isHome:false,
        browserInfo:[],
        config:null,
        lanSelects:[],
        lanFiles: []
      }
    },
    components: {
      Welcome,
      Home,
      BrowserPage,
    },
    methods: {
      //获取配置文件
      fetchConfig() {
        fetch('./cfg.json')
        .then(response => {
          if (response.ok) {
            return response.json();
          }
          throw new Error('Network response was not ok.');
        })
        .then(json => {
          this.config = json;
          document.title = this.config.Opt.Title;
          console.log(this.config.Dev);
          this.$bus.$emit('getShowDevices',this.config.Dev);
          this.fetchLanguage();
        })
        .catch(error => {
          console.error('Error fetching config:', error);
        });
      },

      //获取语言包文件
      fetchLanguage() {
        const fileNames = [];
        const req = require.context('./lan', false, /\.(json)$/);
        this.lanFiles = req.keys().map(path => req(path));
        req.keys().map(path => {
          const fileNmae = path.split('/').pop().replace(/\.[^/.]+$/, '');

          fileNames.push(fileNmae);
        })

        for(let i = 0;i < fileNames.length;i++)
        {
          let lanSelect = {
            short:fileNames[i],
            text:this.lanFiles[i].Language,
          }
          this.lanSelects.push(lanSelect);
        }

        this.$bus.$emit('getGlobalCfg',this.config);
        this.$bus.$emit('getLanSelects',this.lanSelects);
        this.$bus.$emit('getLanFiles',this.lanFiles);
      },

      //获取浏览器信息，HID服务仅支持“Chrome”，“Edge”和“Opera”，不支持“safari”和“FireFox”
      getBrowserInfo() {
        let types = ['edg','firefox','chrome','safari','opera '];
        let userAgent = navigator.userAgent.toLocaleLowerCase();
        var res = [];

        types.forEach(element => {
            if(userAgent.indexOf(element)>0){
                let rule= `${element}`+"\\/([\\d.]+)";
                res.push(element);
                res.push(userAgent.match(rule)[1]);
            }
        });

        if(res.indexOf('chrome') >-1 && res.indexOf('safari') >-1){
            if(res.length===4){
                let temp=[];
                temp.push('chrome');
                temp.push(res[res.indexOf('chrome')+1]);
                res = temp;
            }else{
            res.splice(res.indexOf('chrome'),2);
            res.splice(res.indexOf('safari'),2);
            }
        }

        if(res[0] === "chrome" || 
          res[0] === "edg" ||
          res[0] === "opera")
          {
            this.isSupportHID = true;
          }
          else 
            this.isSupportHID = false;
      },

    },
    created() {
      this.fetchConfig();
      this.getBrowserInfo();
    },
    mounted() {
      this.$bus.$on('enterMain',value => {

        this.isHome = value;
      });
    },
    beforeDestroy() {
      this.$bus.$off('enterMain');
    }
  }
</script>

<style lang="less">
  html,body {
    margin: 0;
    padding: 0;
  }
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #fff;
  }
</style>
