function setKeyFunction(value,index) {
  var addr = 0x60 + index * 4;
  loadArrayBuffer(addr,value); 
}

function setShortCutKey(shortCut,index) {
  var addr = 0x100 + index * 0x20;
  var value = [];
  var str = shortCut.toLowerCase();
  var cnt = 0;
  if(str.indexOf("ctrl") !== -1)
  {
    value[cnt++] = 0x80;
    value[cnt++] = 0x01;
    value[cnt++] = 0x00;
  }



  loadArrayBuffer(addr,value);
}

export default {
  requestDevice,
  setReport,
  setMaxDPI,
}