<template>
  <div>
    <el-container>
        <el-header style="height: 15vh;">
          <select class="lanSelect" v-model="selectLan" @change="lanChange">
            <option v-for="item in lan" :key="item.short" :value="item.short" >{{ item.text }}</option>
          </select>
        </el-header>
        <el-main>
          <welcome-main />
        </el-main>
        <el-footer style="height: 10vh;">
          <div class="footer">Footer</div>
        </el-footer>
    </el-container>
  </div>
</template>

<script>
  import WelcomeMain from '../components/WelcomeMain';

  export default {
  components: { WelcomeMain },
    comments: {
      WelcomeMain,
    },
    data() {
      return {
        lan:[],
        selectLan:null,
        lanFiles:null,
      }
    },
    methods: {
      lanChange() {
        console.log(this.selectLan,this.lan);
        let lanFile = {};

        for(let i = 0;i < this.lan.length;i++)
        {
          if(this.lan[i].short === this.selectLan)
          {
            lanFile = this.lanFiles[i];
            break;   
          }
        }
        console.log(lanFile);
        let elements = document.querySelectorAll('[lan]');

        // 遍历每个需要翻译的元素
        elements.forEach(element => {
        let id = element.getAttribute('lan');  
        if (lanFile[id]) {
            element.innerText = lanFile[id]; // 使用翻译后的文本替换原来的文本
        }
        });
      },
    },
    mounted() {
      this.$bus.$on('getLanSelects',value => {

        this.lan = value;
        this.selectLan = value.length ? value[0].short : '';
      });

      this.$bus.$on('getLanFiles',value => {

      this.lanFiles = value;
      });
    },
    beforeDestroy() {
      this.$bus.$off('getLanSelects');
      this.$bus.$off('getLanFiles');
    }
  }
</script>

<style lang="less" scoped>
.el-header {
  background-color: #131313;
  padding: 0 0;
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.el-main {
  background-color: #353535;
  color: #fff;
  height: 75vh;
}
.el-footer {
  background-color: #353535;
  color: #fff;
  text-align: center;
  .footer {
    margin: 0;
    position: relative;
    top:20px;
  }
}
select
  {
    right:20px;
    margin:0 20px;
    position: absolute;
    background-color: #131313;
    color: #fff;
  }
</style>