<template>
  <div>
    <el-menu
      class="el-menu-demo"
      mode="horizontal"
      background-color="#131313"
      text-color="#fff"
      active-text-color="#ff7F00"
      >
      <el-menu-item 
        @click="clickMenu(item)" 
        v-for="item in menuData" 
        :key="item.name" 
        :index="item.name"
        >
        <i :class="`el-icon-${item.icon}` "></i>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        menuData : [
          {
            path:'/custom',
            name: 'custom',
            label: '按钮',
            icon: 's-home',
            url: 'Custom/Custom'
          },
          {
            path:'/sensor',
            name: 'sensor',
            label: '传感器',
            icon: 's-home',
            url: 'Sensor/Sensor'
          },
          {
            path:'/macro',
            name: 'macro',
            label: '宏',
            icon: 'macrom',
            url: 'Macro/Macro'
          },
          {
            path:'/light',
            name: 'light',
            label: '灯光',
            icon: 'sunny',
            url: 'Light/Light'
          },
          {
            path:'/setting',
            name: 'setting',
            label: '设置',
            icon: 'setting',
            url: 'Setting/Setting'
          },          
        ]
      }
    },
    methods: {
      //点击菜单
      clickMenu(item)
      {

        //当页面的路由与跳转的路由不一致的时候才可以跳转
        if(this.$route.path !== item.path && 
          !(this.$route.path === '/custom' && (item.path === '/')))
        {
          this.$router.push(item.path)
        }
      }
    },
    computed: {
      //判断是否需要灯光显示
      dispalyLight() {
        return this.menuData.filter(item => item.name !== 'light')
      }
    }
  }
</script>

<style lang="less" scoped>
.el-menu {
  margin-left: 15%;
  position: absolute;
  bottom: 85%;
  width: 400px;
}
.el-menu-item {
  width: 60px;
  height: 60px;
  margin-left: 20px;
}
</style>