<template>
    <div>
        <h1>我是Custom</h1>
        <el-cascader 
        v-show="loadFinish" 
        :options="keys" 
        :show-all-levels="false" 
        @change="selectChange"></el-cascader>
        <el-dialog
          title="提示"
          :visible.sync="dialogVisible"
          width="30%"
          :before-close="handleClose">
          <span>这是一段信息</span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
    </div>
</template>

<script>
  import CommonHeader from '../components/CommonHeader'
  import CommonAside from '../components/CommonAside'

  export default {
    data() {
      return {
        loadFinish:false,
        keys:[],
        dialogVisible: false
      }
    },
    methods: {
      selectChange(e) {
        if(e[0] === "4")
        {
          this.dialogVisible = true;          
        }
        console.log(e);
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    },
    mounted() {
      this.$bus.$on('getLanFiles',value => {
        console.log('Lan',value);
        this.loadFinish = true;
        this.keys = value[1].KeysFun;
      });
    },
    beforeDestroy() {
      this.$bus.$off('getLanFiles');
    }
  }
</script>

