<template>
  <div>
    <p >灯光颜色</p>
    <br>
    <el-image 
      src='./img/8General/Color/colorbar.png' 
      alt="Color Picker"
      @click="getPixelColor($event)" 
      ref="imageElement">
    </el-image>
    <br>
    <span class="demonstration">颜色预览</span>
    <el-row>
      <el-col :span="20" class="box" :style="{backgroundColor:boxColor}">
      </el-col>
      <el-col :span="28">
        <el-row>
          <el-input-number v-model="colorR" @change="colorRChange" :min="0" :max="255" size="mini"></el-input-number>
        </el-row>
        <el-row>
          <el-input-number v-model="colorG" @change="colorGChange" :min="0" :max="255" size="mini"></el-input-number>
        </el-row>
        <el-row>
          <el-input-number v-model="colorB" @change="colorBChange" :min="0" :max="255" size="mini"></el-input-number>
        </el-row>
      </el-col>
    </el-row>
    <br>
    <span class="demonstration">预设颜色</span>
    <el-image 
      v-for="(item,index) in 14"
      :key="index"
      :src='`./img/8General/Color/color${item}.png`' 
      alt="Color Picker"
      @click="getPixelColor($event)" 
      ref="imageElement">
    </el-image>
  </div>



</template>

<script>

  export default {
      data() {
        return {
          boxColor:'#000',
          colorR:0,
          colorG:0,
          colorB:0,
        };
      },
      methods: {
        getPixelColor(event) {
          const imgElement = event.target;
          const canvas = document.createElement('canvas');
          const ctx = canvas.getContext('2d');
    
          canvas.width = imgElement.naturalWidth;
          canvas.height = imgElement.naturalHeight;
          ctx.drawImage(imgElement, 0, 0);
    
          const pixelData = ctx.getImageData(
            event.offsetX,
            event.offsetY,
            1,
            1
          ).data;
          
          const rgb = `rgb(${pixelData[0]}, ${pixelData[1]}, ${pixelData[2]})`;
          this.boxColor = rgb;
          this.colorR = pixelData[0];
          this.colorG = pixelData[1];
          this.colorB = pixelData[2];
        },
        colorRChange() {
          const rgb = `rgb(${this.colorR}, ${this.colorG}, ${this.colorB})`;
          this.boxColor = rgb;
        },
        colorGChange() {
          const rgb = `rgb(${this.colorR}, ${this.colorG}, ${this.colorB})`;
          this.boxColor = rgb;
        },
        colorBChange() {
          const rgb = `rgb(${this.colorR}, ${this.colorG}, ${this.colorB})`;
          this.boxColor = rgb;
        }
      }
    };
</script>

<style lang="less" scoped>
.box {
  border: 0px solid #000; /* 边框样式 */
  padding: 20px;          /* 内边距 */
  width: 100px;           /* 宽度 */
  height: 100px;          /* 高度 */
  margin-left:40%;
}
</style>